import * as React from "react";
import MainActions from "../../actions/main-actions";
;
class MainHeader extends React.Component {
    render() {
        return (React.createElement("ul", {className: "nav nav-tabs"}, this.props.links.map(link => (React.createElement("li", {className: link.viewId === this.props.currentViewId ? "active" : "", key: link.viewId, onClick: this.onLinkClicked.bind(this, link.viewId)}, React.createElement("a", {href: "#"}, link.viewTitle))))));
    }
    onLinkClicked(viewId) {
        MainActions.getCurrentView(viewId);
    }
}
export default MainHeader;



