import { ReduceStore } from "flux/utils";
import Dispatcher from "../dispatcher/app-dispatcher";
import { Map } from "immutable";
class HomeStore extends ReduceStore {
    getInitialState() {
        return Map({
            cities: [],
            selectedCityIds: [],
            isLoading: false,
            weather: null
        });
    }
    reduce(state, action) {
        switch (action.type) {
            case "home/cities":
                return state.set("isLoading", true);
            case "home/cities/success":
                return state
                    .set("isLoading", false)
                    .set("cities", action.cities);
            case "home/cities/error":
                return state.set("isLoading", false);
            case "home/city/change":
                return state
                    .set("isLoading", true)
                    .set("selectedCityIds", action.selectedCityIds);
            case "home/city/weather":
                return state
                    .set("isLoading", false)
                    .set("weather", action.weather);
            default:
                return state;
        }
    }
}
export default new HomeStore(Dispatcher);



